import React, { Component } from "react"
import { Label, Input, FormGroup, Button } from "reactstrap"
import { X } from "react-feather"
import PerfectScrollbar from "react-perfect-scrollbar"
import classnames from "classnames"

class DataListSidebar extends Component {
  state = {
    _id: "",
    name: "Level",
    description: "description",
    img: "",
    file: null,
  }

  addNew = false

  componentDidUpdate(prevProps, prevState) {
    if (this.props.data !== null && prevProps.data === null) {
      if (this.props.data._id !== prevState._id) {
        this.setState({ _id: this.props.data._id })
      }
      if (this.props.data.name !== prevState.name) {
        this.setState({ name: this.props.data.name })
      }
      if (this.props.data.description !== prevState.description) {
        this.setState({ description: this.props.data.description })
      }
      console.log("this.props.data", this.props.data);
      if (this.props.data.imgUrl !== prevState.imgUrl) {
        this.setState({ img: this.props.data.imgUrl })
      }
    }
    if (this.props.data === null && prevProps.data !== null) {
      this.setState({
        id: "",
        name: "new Level",
        description: "description",
        file: null,
        img: "",
      })
    }
    if (this.addNew) {
      this.setState({
        id: "",
        name: "new Level",
        description: "description",
        img: "",
        file: null,
      })
    }
    this.addNew = false
  }

  handleSubmit = async ( obj )=> {
    if (this.props.data !== null) {
      this.props.updateData(obj)
    } else {
      this.addNew = true;
      this.props.addData(obj)
    }
    let params = Object.keys(this.props.dataParams).length
      ? this.props.dataParams
      : { page: 1, perPage: 4 }
    this.props.handleSidebar(false, true)
    this.props.getData(params)
  }

  render() {
    let { show, handleSidebar, data } = this.props
    let { name, description, img } = this.state
    return (
      <div
        className={classnames("data-list-sidebar", {
          show: show
        })}>
        <div className="data-list-sidebar-header mt-2 px-2 d-flex justify-content-between">
          <h4>{data !== null ? "UPDATE DATA" : "ADD NEW DATA"}</h4>
          <X size={20} onClick={() => handleSidebar(false, true)} />
        </div>
        <PerfectScrollbar
          className="data-list-fields px-2 mt-3"
          options={{ wheelPropagation: false }}>
            <FormGroup className="text-center">
              <p className="text-center text-info">size 200 * 200</p>
              <img className="img-fluid" src={img} alt={name} />
              <div className="d-flex flex-wrap justify-content-between mt-2">
                <label
                  className="btn btn-flat-primary"
                  htmlFor="update-image"
                  color="primary">
                  Upload Image
                  <input
                    type="file"
                    id="update-image"
                    hidden
                    onChange={e =>{
                      console.log(e.target.files);
                      if(e.target.files[0]) {
                        this.setState({
                          img: URL.createObjectURL(e.target.files[0]),
                          file: e.target.files[0]
                        })
                      }
                    }}
                  />
                </label>
                <Button
                  color="flat-danger"
                  onClick={() => this.setState({ img: "", file: null })}>
                  Remove Image
                </Button>
              </div>
            </FormGroup>
          <FormGroup>
            <Label for="data-name">Name</Label>
            <Input
              type="text"
              value={name}
              placeholder="Primary 1"
              onChange={e => this.setState({ name: e.target.value })}
              id="data-name"
            />
          </FormGroup>
          <FormGroup>
            <Label for="data-category">Description</Label>
            <Input
              type="textarea"
              id="data-category"
              value={description}
              onChange={e => this.setState({ description: e.target.value })}>
            </Input>
          </FormGroup>
        </PerfectScrollbar>
        <div className="data-list-sidebar-footer px-2 d-flex justify-content-start align-items-center mt-1">
          <Button color="primary" onClick={() => this.handleSubmit(this.state)}>
            {data !== null ? "Update" : "Submit"}
          </Button>
          <Button
            className="ml-1"
            color="danger"
            outline
            onClick={() => handleSidebar(false, true)}>
            Cancel
          </Button>
        </div>
      </div>
    )
  }
}
export default DataListSidebar
